.listBox {
  padding: 2vh;
  margin-top: 1vh;
  background: #99999955;
  text-align: center;
  border-radius: 2px;
  box-shadow: 0 4px 8px 0 rgb(0 0 0), 0 6px 20px 0 rgb(0 0 0);
  font-family: "Courier New", Courier, monospace;
  .listBox-header {
    width: 80%;
    background-color: rgba(36, 109, 179, 0.9);
    font-weight: bold;
    padding: 1.1vh;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1rem;
  }
  .listBox-body {
    text-align: left;
    .listBox-item {
      margin-bottom: 0.6rem;
      .listBox-item-label {
        &:hover,
        &:hover * {
          font-weight: bold;
        }
        i {
          margin-right: 0.5rem;
        }
        a {
          text-decoration: none;
        }
      }
    }
  }
}
